import React, { useState, useEffect } from "react";
import MaterialTable from "material-table";
import { Image, Modal, Button } from "semantic-ui-react";
import { useAppStore } from "../../app.state";
import defaultImg from '../../images/default.png'
import axiosInstance from "../../axios/axios";


function ClientTable({ data, editPortal, updateSite }) {
  const [open, setOpen] = useState(false);
  const [client, setClient] = useState({})
  const setOpenClient = useAppStore((state) => state.setOpenClient);
  const [editClientData, setEditClientData] = useAppStore((state) => [
    state.editClientData,
    state.setEditClientData,
  ]);

  const delClient = async (id) => {
    try {
      await axiosInstance.delete(`clients/${id}`)
      setOpen(false)
      
    } catch(err) {
      console.log(err)
    }
  }

  useEffect(() => {
    updateSite()
  }, [open])


  return (
    <>
      <MaterialTable
        test={open}
        title="Kundenliste"
        columns={[
          {
            title: "Logo",
            field: "image",
            render: (rowData) => {
              console.log(rowData.image)
              return (
                <Image bordered src={rowData.image} style={{ height: "30px", background:'transparent' }} rounded />
              )
            },
          },
          { title: "Kunde", field: "name" },
          { title: "Kurz", field: "short" },
        ]}
        data={data}
        options={{
          actionsColumnIndex: -1,
          sorting: true,
          exportButton: true,
          pageSize: 10,
          padding: 'dense'
        }}
        actions={[
          {
            icon: "edit",
            tooltip: "Projektdetails",
            onClick: (event, rowData) => {
              editPortal(true);
              console.log(rowData);
              setEditClientData(rowData);
            },
          },
          {
            icon: "delete",
            tooltip: "Projekt lschen",
            onClick: (event, rowData) => {
              console.log(rowData);
              setClient(rowData)
              setOpen(true)
            },
          },
          {
            icon: "add",
            isFreeAction: true,
            onClick: () => {
              setOpenClient(true);
            },
          },
        ]}
      />
      <Modal open={open} onClose={() => setOpen(false)}>
        <Modal.Header>Kunde lschen</Modal.Header>
        <Modal.Content>
          Das Project{" "}
          <strong>
            {client.name}
          </strong>{" "}
          wirklich lschen ?
        </Modal.Content>
        <Modal.Actions>
          <Button negative onClick={() => setOpen(false)}>
            Abbrechen
          </Button>
          <Button positive onClick={() => delClient(client.id)}>
            Lschen
          </Button>
        </Modal.Actions>
      </Modal>
    </>
  );
}

export default ClientTable;
